.button {
	margin: 10px 0 20px;
	padding: 8px 10px;
	border: 1px solid rgb(76, 75, 75);
	border-radius: 20px;
	cursor: pointer;
	color: rgb(76, 75, 75);
	transition: all 250ms cubic-bezier(0.075, 0.82, 0.165, 1);
 }
 .button:hover,
 .button:focus {
	background-color: rgb(76, 75, 75);
	color: white;
	border: 1px solid #fff;
 }